.xl-home {
  padding: 0 25px 25px;

  .xl-banner {
    position: relative;
    height: 200px;
    width: 100%;
    overflow: hidden;

    img {
      position: absolute;
      left: 50%;
      top: 50%;
      max-height: 80%;
      transform: translate(-50%, -50%);
    }
  }
}

.xl-home-body {
  &::after {
    content: '';
    display: table;
    clear: both;
  }

  .xl-home-left {
    float: left;
    width: 60%;
  }

  .xl-home-right {
    float: right;
    width: 35%;
  }

  .ht-line2 {
    position: relative;
    line-height: 1.4em;
    height: 2.8em;
    transition: height 0.5s;
    overflow: hidden;
    padding-right: 5em;

    .ht-line2-btn {
      position: absolute;
      right: 0;
      top: 0;
    }
  }

  .ht-line2.open {
    height: auto;
  }
}